<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
      </div>
    <div class='tap'>
      <HorizontalTap class='center' :data='data2' :active="3" style="padding-left: 100px;"></HorizontalTap>
    </div>
    <PDF v-if="pdfFlg" :pdf-url="pdfUrl"></PDF>
    <div class="col-md-10 col-md-offset-1 mt-6">
      <el-tabs v-model="activeName" type="border-card" class="mb-4">
        <el-tab-pane name="project">
          <span slot="label"><i class="el-icon-connection"></i> 项目相关指导材料</span>
          <div class="row mb-5">
            <div class="col-md-12 col-xs-12 pl-6 mt-4">
              <div class="row">
                <span class="line"></span><span>学习视频</span>
              </div>
              <div class="row mt-4">
                <div class="col-md-2 text-center" v-for="(item, key) in videoList" v-bind:key="key" @click="gotoVideo(item)">
                  <img class="video-img" src="../../../../assets/img/video-material.png">
                  <a class="font-small">{{item.materialName}}</a>
                </div>
              </div>
              <div class="row mt-6">
                <span class="line"></span><span>文档模板</span>
              </div>
              <div class="row mt-4">
                <div class="col-md-2 text-center" v-for="(item, key) in docList" v-bind:key="key" @click="gotoPDF(item)">
                  <img class="video-img" src="../../../../assets/img/word.png">
                  <a class="font-small">{{item.materialName}}</a>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane name="all">
          <span slot="label"><i class="el-icon-tickets"></i> 知识点学习材料</span>
          <div class="row mt-3 mb-3" v-for="(item, key) in materialTypes" v-bind:key="key">
            <div class="col-md-4 col-sm-6 col-xs-12 pull-left pl-4 pr-4">
              <div class="swiper-container" style="height: 296px;" :class="[{ java_bg: item.name=='Java' }]">
                <div class="pic_left pl-6">
                  <h3 class="font-bold" style="">{{item.name}}</h3>
                  <p>{{item.describes}}</p>
                </div>
                <img :src="item.imageUrl">
              </div>
            </div>
            <div class="col-md-8 col-xs-12 pull-left">
              <div class="java_tit mb-4">
                <span>{{item.name}}教程</span>
                <span class="pull-right" style="color: #000000;background: #ffffff;margin-top:1px">More<i class="el-icon-caret-right"></i></span>
              </div>
              <div class="row pl-4 pr-5" v-for="(material, index) in item.materialsDetail" v-bind:key="index">
                <span class="pull-left java_color">[{{item.name}}]</span><a class="ml-3" :href="material.materialUrl" target="_blank">{{material.materialName}}</a><a :href="material.materialUrl" target="_blank" class="pull-right font-small">查看详情</a>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--<div class="col-md-12 mt-5 mb-6">-->
      <!--<span class="col-md-5 col-md-offset-1 font-bold">热门搜索: <span class="ml-3" v-for="(item, index) in hotSearch" v-bind:key="index">{{item}}</span></span>-->
      <!--<span class="col-md-2 col-md-offset-3 pull-left">-->
        <!--<el-input-->
        <!--placeholder="请输入内容"-->
        <!--prefix-icon="el-icon-search"-->
        <!--v-model="materialSearch">-->
        <!--</el-input>-->
      <!--</span>-->
    <!--</div>-->
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import PDF from '../../../../components/learning-library/pdf'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    PDF
  },
  mounted () {
    this.loadData()
  },
  methods: {
    loadData () {
      let me = this
      me.projectId = localStorage.getItem('projectId')
      me.$http.post('/doproject/materialsInfoByProject', { 'sourceType': 0, 'projectId': me.projectId }, res => {
        if (res.success) {
          for (var i = 0; i < res.data.length; i++) {
            if (res.data[i].materialType === 1) {
              me.videoList.push(res.data[i])
            } else if (res.data[i].materialType === 0) {
              me.docList.push(res.data[i])
            }
          }
          me.$http.post('/doproject/materialsInfoByCommunity', { 'sourceType': 1 }, ressns => {
            console.log(ressns)
            me.materialTypes = ressns.data
          })
        }
      })
    },
    gotoVideo (obj) {
      localStorage.setItem('materialVideo', obj.materialUrl)
      localStorage.setItem('materialId', obj.id)
      localStorage.setItem('materialName', obj.materialName)
      window.open('video-player.html', '_blank')
    },
    gotoPDF (obj) {
      this.pdfFlg = false
      this.$nextTick(() => {
        this.pdfUrl = obj.materialUrl
        this.pdfFlg = true
      })
    }
  },
  data () {
    return {
      activeName: 'project',
      projectId: '',
      hotSearch: ['VUE', 'SpringMVC', 'Jquery', 'SpringBoot'],
      materialSearch: '',
      materialTypes: ['Java', 'Web/H5', 'C++'],
      videoList: [],
      docList: [],
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_blank'
      }, {
        name: '成长轨迹',
        url: 'growth-locus.html',
        type: '_self'
      }],
      pdfFlg: false,
      pdfUrl: ''
    }
  }
}
</script>
<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 210px;
    color: #fff;
    text-align: center;
    img{
      padding-top: 110px;
    }
  }
  .java_bg {
    background: #e66b46;
  }
  .java_color {
    color: #e66b46;
  }
  .pic_left {
    padding-bottom: 20px;
    padding-top: 10px;
    text-align: left;
    height: 90px;
    position: absolute;
    top: 0;
    left: 1rem;
    background: none;
    z-index: 10;
    padding-right: 0.4rem;
    h3{
      color:#ffffff;
      font-size: 25px
    }
    p{
      font-size: 16px;
      font-weight: 500;
    }
  }
  .java_tit {
    border-top: 2px solid #e66b46;
    span {
      padding: 10px 20px;
      display: inline-block;
      background: #e66b46;
      color: #ffffff;
    }
  }
  .line{
    float: left;
    width: 3px;
    height: 16px;
    background: #6612cf;
    overflow: hidden;
    margin-right: 5px;
  }
  .video-img{
    padding: 20px;
    width: 100%;
    border-radius: 25px;
  }
</style>
<style>
  .el-tabs--border-card > .el-tabs__header {
    background-color: #ffffff!important;
    border-bottom: none!important;
    margin: 0;
  }
  .el-tabs--border-card {
    background: #FFFFFF;
    border: none!important;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    border-radius: 5px;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border: none!important;
    margin-top: -1px;
    color: #909399;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border: 1px solid transparent;
    margin-top: -1px;
    color: #909399;
    font-size: 20px;
    padding: 20px;
  }
</style>
